<template>
  <div class="reader">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item><router-link to="/admin/home">首页</router-link></a-breadcrumb-item>
        <a-breadcrumb-item>读者管理</a-breadcrumb-item>
      </a-breadcrumb>
    </div>

    <div class="manage">
      <a-form layout="inline" class="margin_bottom_20">
        <a-form-item label="姓名">
          <a-input v-model="params.name"></a-input>
        </a-form-item>
        <a-form-item label="手机">
          <a-input v-model="params.phone"></a-input>
        </a-form-item>
        <a-form-item label="用户状态">
          <a-select
            v-model="params.status"
            :options="status"
            allowClear
          ></a-select>
        </a-form-item>
        <a-form-item label="用户权限">
          <a-select
            v-model="params.auth"
            :options="auth"
            allowClear
          ></a-select>
        </a-form-item>
        <a-form-item>
          <a-button type="primary" style="margin-right: 12px;" @click="$refs.table.refresh(true)">搜索</a-button>
          <a-button @click="reset">重置</a-button>
        </a-form-item>
      </a-form>
    </div>

    <div class="btn_group margin_bottom_20">
      <a-button type="primary" @click="toExamine">读者审核</a-button>
      <div>
        <a-button type="primary" @click="banReader(selectedRowKeys, 2)">批量禁用</a-button>
        <a-button type="primary" style="margin-left: 20px;" @click="banReader(selectedRowKeys, 1)">批量启用</a-button>
      </div>
    </div>

    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
      :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
    >
      <template slot="gender" slot-scope="text">
        {{ text.gender === 1 ? '男' : '女'}}
      </template>
      <template slot="auth" slot-scope="text">
        {{ text.auth === 1 ? '正常' : '已禁用' }}
      </template>
      <template slot="status" slot-scope="text">
        {{ formatStatus(text.status)}}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="viewReader(text.id)">查看</a-button>
        <a-button type="link" @click="banReader(text)">{{ text.auth === 1 ? '禁用' : '启用' }}</a-button>
      </template>
    </s-table>

    <!-- 查看读者详情 -->
    <a-modal
      v-model="readerDetailVisible"
      :footer="null"
      title="读者详情"
      width="50%"
    >
      <a-row class="reader_row">
        <a-col>
          <span>姓名：</span>
          <span>{{readerDetail.name}}</span>
        </a-col>
        <a-col>
          <span>手机：</span>
          <span>{{readerDetail.phone}}</span>
        </a-col>
        <a-col>
          <span>身份证号：</span>
          <span>{{readerDetail.code}}</span>
        </a-col>
        <a-col>
          <span>用户状态：</span>
          <span>{{formatStatus(readerDetail.status)}}</span>
        </a-col>
        <a-col>
          <span>身份证照片：</span>
          <span>安全与健康</span>
        </a-col>
      </a-row>
      <h2>借阅记录</h2>
      <div v-if="recordList.length > 0">
        <template v-for="item in recordList">
          <div :key="item.id">
            <a-row>
              <a-col :span="6">时间</a-col>
              <a-col :span="6">操作</a-col>
              <a-col :span="6">书籍</a-col>
              <a-col :span="6">状态</a-col>
            </a-row>
            <a-row>
              <template>
                <a-col :span="6">{{item.create_time}}</a-col>
                <a-col :span="6">{{formatTime(item.end_time)}}</a-col>
                <a-col :span="6">{{item.book.book_name}}</a-col>
                <a-col :span="6">{{formatStatus(item.status)}}</a-col>
              </template>
            </a-row>
          </div>
        </template>
      </div>
      <div v-else>
        暂时没有记录
      </div>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Reader',
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '姓名',
        dataIndex: 'name'
      },
      {
        title: '性别',
        scopedSlots: { customRender: "gender"},
      },
      {
        title: '手机',
        dataIndex: 'phone'
      },
      {
        title: '用户状态',
        scopedSlots: { customRender: "status"},
      },
      {
        title: '用户权限',
        scopedSlots: { customRender: "auth"},
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ]

    let params = {
      status: '',
      name: '',
      phone: '',
      auth: ''
    }

    let recordParams = {
      offset: 0,
      limit: 10,
      book_id: null
    }

    return {
      columns,
      params,
      recordParams,
      readerDetailVisible: false,
      selectedRowKeys: [],
      loadData: paramters => {
        return this.getReaderList(
          Object.assign({}, this.params, paramters)
        )
      }
    }
  },
  computed: {
    ...mapState('reader', ['readerDetail', 'recordList']),
    formatStatus() {
      return code => {
        return code === 1 ? '正常' : code === 2 ? '已禁用' : '未审核'
      }
    },
    formatTime() {
      return (e) => {
        const now = Number(new Date().getTime() / (86000 * 1000))
        const et = Number(new Date(e).getTime() / (86000 * 1000))

        if (now - et > 0) {
          return `超时${Math.floor(now - et)}天`
        } else {
          return '正常'
        }
      }
    },
    status() {
      return [
        {
          label: '全部',
          value: ''
        },
        {
          label: '正常',
          value: 1
        },
        {
          label: '已禁用',
          value: 2
        }
      ]
    },
    auth() {
      return [
        {
          label: '全部',
          value: ''
        },
        {
          label: '正常',
          value: 1
        },
        {
          label: '已禁用',
          value: 2
        }
      ]
    }
  },
  methods: {
    ...mapActions('reader', {
      getReaderList: 'getReaderList',
      getReaderDetail: 'getReaderDetail',
      getReaderRecord: 'getReaderRecord',
      ban: 'ban'
    }),
    viewReader(id) {
      this.recordParams.book_id = id
      Promise.all([this.getReaderDetail(id), this.getReaderRecord(this.recordParams)]).then(res => {
        this.readerDetailVisible = true
      })
    },
    banReader(text, auth) {
      console.log(text instanceof Object)
      let id = []
      if (text instanceof Array) {
        id = [...text]
      } else {
        id.push(text.id)
        auth = text.auth === 1 ? 2 : 1
      }
      this.ban({
        id,
        auth,
      }).then(res => {
        this.$message.success('操作成功')
        this.$refs.table.refresh(true)
      })
    },
    toExamine() {
      this.$router.push({
        name: 'Reader Examine'
      })
    },
    reset() {
      this.params = {
        status: '',
        name: '',
        phone: '',
        auth: ''
      }
      this.$refs.table.refresh(true)
    },
    onSelectChange(val) {
      this.selectedRowKeys = val
    }
  }
}
</script>
<style lang="scss" scoped>
.manage {
  padding: 0 30px;
}
.btn_group {
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
}
.reader_row {
  .ant-col {
    margin-bottom: 14px;
  }
}
.ant-select {
  width: 174px;
}
</style>